import { defineStore } from 'pinia';
import link from '../api/Link.js';
import apiUrl from '../api/url.js';
const useRtStore = defineStore('rt', {
   
    state: () => ({
        choose: false,
        dialogFormVisible: false,
        dialogFormVisibleNew:false,
        stamp: {
            id:1
        },
        tableData:[]
    }),

    //  getters
    getters: {
        // getter  state 
       
    },

    //  actions
    actions: {
        change(){
        this.choose=!this.choose
        },
        setStamp(v:any) {
         this.stamp=v
        },
        listStamp() {
            link(apiUrl.list, "post", null, null,localStorage.getItem('token')).then((res: any) => {
                this.tableData = res.data
            })
    }
    }
})

export default useRtStore

